::ng-deep mat-progress-bar .mat-progress-bar-fill::after {
  @apply rounded-md;
}
::ng-deep mat-progress-bar .mat-progress-bar-buffer {
  @apply rounded-md;
}

.celebration-overlay {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 3rem;
  font-weight: bold;
  color: #22c55e;
  text-shadow: 0 0 10px rgba(34, 197, 94, 0.8);
  pointer-events: none;
  z-index: 10;
  display: none;
}
.celebration-overlay.show {
  display: block;
  animation: celebrate100 2s ease-in-out forwards;
}

@keyframes celebrate100 {
  0% {
    transform: translate(-50%, -50%) scale(0.5);
    opacity: 0;
  }
  25% {
    transform: translate(-50%, -50%) scale(1.2);
    opacity: 1;
  }
  50% {
    transform: translate(-50%, -50%) scale(1.4);
    opacity: 1;
  }
  75% {
    transform: translate(-50%, -50%) scale(1.2);
    opacity: 1;
  }
  100% {
    transform: translate(-50%, -50%) scale(0.8);
    opacity: 0;
    display: none;
  }
}
